export default function Vmodal(props) {
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const btnPtest = props.btnprm;
  const btnStest = props.btnsec;
  let pbutton;
  let sbutton;


  if (btnPtest) {
    pbutton = (
      <Button variant="contained" onClick={handleOpen} className="vbtn">{props.btnprm}</Button>
    );
  }  
  if (btnStest) {
    sbutton = (
      <Button variant="outlined" onClick={handleClose} className="vbtn" >{props.btnsec}</Button>
    );
  }
   if (btnPtest && btnStest){
    sbutton = (
      <Button variant="outlined" onClick={handleClose} className="vbtn vspace" >{props.btnsec}</Button>
    );
}

  return (
  <div className="vmodal-btn">
    <Button variant="contained" className="vbtn" onClick={handleOpen}>
      {props.btntxt}
    </Button>
    <Modal
      aria-labelledby="transition-modal-title"
      aria-describedby="transition-modal-description"
      className={'vmodal '}
      open={open}
      onClose={handleClose}
      closeAfterTransition
      BackdropComponent={Backdrop}
      BackdropProps={{
          timeout: 500,
      }}
    >
      <Fade in={open}>
        <div className={'vcontent'}>
          <div className="vhdr">
            <IconButton aria-label="Close" onClick={handleClose} className="vbtn vbtn-close">
              <img src={IconClose} alt="Close" className="vimg-close" />
            </IconButton>
          </div>
          <div className="vscroll">
            <div className="vbody">
              <h3 class="h2">{props.title}</h3>
						  <div class="line"></div>

              <div className="main">{props.children}</div>

            </div>
            <div className="vftr">
              {sbutton}{pbutton}<br />
            </div>
          </div>
        </div>
      </Fade>
    </Modal>
  </div>
  );
}